import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';


let 박스 = styled.div`
  padding : 20px;
`;

let 제목 = styled.h4`
  font-size : 25px;
  color : ${ props => props.색상 }
`;

function Detail(props){

    let [ alert, changeAlert ] = useState(true);
    let [ inputData, inputData변경 ] = useState('');

    let history = useHistory();
    let { id } = useParams();

    let findItem = props.shoes.find(function(product){
      return product.id == id
    });

    useEffect(()=>{
      let 타이머 = setTimeout(()=> { changeAlert(false)}, 2000);
      return ()=>{ clearTimeout(타이머) }
    }, []);

    return (
      <div className="container">
        <박스>
          <제목 색상='blue'> 상세페이지 </제목>
        </박스>
        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{findItem.title}</h4>
            <p>{findItem.content}</p>
            <p>{findItem.price + '원'}</p>

            <Info stock={props.stock} id={findItem.id}></Info>

            <button className="btn btn-danger" onClick={()=>{
              var newStock = props.stock;
              newStock[findItem.id] = newStock[findItem.id] - 1;
              props.changeStock(newStock);
            }}>주문하기</button> 
            &nbsp;
            <button className="btn btn-danger" onClick={ ()=>{
                history.push('/')
            } }>뒤로가기</button> 
          </div>
        </div>

        {
          alert === true
          ? (<div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
          </div>)
          : null
        }
      </div> 
      
    )
  }


  function Info(props){
    return(
      <p>재고 : { props.stock[props.id] }</p>
    )
  }


  export default Detail